<template>
  <div class="login-container blue-theme">
    <section>
      <van-icon class="backBtn" name="arrow-left" @click="$router.back()" />
      <div class="login-title">
        <p class="hello">绑定手机号</p>
        <p>您的账号尚未绑定手机号，请绑定</p>
      </div>
      <div class="login-tabs">
        <div class="tabs-content">
          <div class="tab-content">
            <login-input
              max-length="11"
              v-model="phoneData.phone"
              type="number"
              placeholder="手机号码"
              @submit="doBind"
            >
              <template slot="left">
                <div class="phone-prefix">
                  {{ currPrefix }}
                </div>
              </template>
            </login-input>
            <div class="verify-code">
              <login-input
                max-length="4"
                v-model="phoneData.code"
                type="number"
                placeholder="验证码"
                @submit="doBind"
              >
              </login-input>
              <a class="get-code" @click="getcaptcha()">{{
                count != 61 ? count + 'S' : '获取'
              }}</a>
            </div>
          </div>
        </div>
      </div>
      <app-button class="login-btn" :disabled="btnDisabled" @click="doBind">
        立即绑定
      </app-button>
    </section>
  </div>
</template>
<script>
import appButton from '@components/app-button/index.vue';
import LoginInput from '@components/login-input';
import userService from '@services/user-service';
import { sendCode } from '@api/sso';
// import NetWork from '@yl-app-jssdk/network';
// import Plugin from '@common/plugin';
// import SsoCenterService from '@services/sso-center-service';
// import DeviceService from '@services/device-service';

export default {
  name: 'BindPhone',
  components: {
    appButton,
    LoginInput,
  },
  data() {
    return {
      count: 61,
      currPrefix: '+86',
      accountData: {
        account: '',
        password: '',
        passwordType: 'password',
      },
      phoneData: {
        phone: '',
        code: '',
      },
      btnDisabled: true,
    };
  },

  watch: {
    'phoneData.phone': function() {
      this.btnDisabled = !(this.phoneData.phone && this.phoneData.code);
    },
    'phoneData.code': function() {
      this.btnDisabled = !(this.phoneData.phone && this.phoneData.code);
    },
  },

  methods: {
    getcaptcha() {
      if (!this.phoneData.phone) {
        this.$toast('请输入正确的手机号');
        return false;
      }
      if (this.count != 61) return;

      return sendCode(this.phoneData.phone, 'bind')
        .then(() => {
          //获取验证码。。。
          this.count--;

          this.countDown();
        })
        .catch(err => {
          console.error(err);
          this.$toast('获取验证码失败，请重试');
        });
    },
    countDown() {
      this.timer = setTimeout(() => {
        if (this.count > 0) {
          this.count--;
          this.countDown();
        } else {
          this.count = 60;
        }
      }, 1000);
    },

    doBind() {
      if (!this.phoneData.phone || !this.phoneData.code) {
        this.$toast('请输入手机号和验证码', 2000);
        return false;
      }

      const _query = this.$route.query || {};
      const _ticket = _query.ticket || '';

      return userService
        .bindPhone({
          ticket: _ticket,
          countryCode: this.currPrefix.replace('+', ''),
          mobile: this.phoneData.phone,
          smsCode: this.phoneData.code,
        })
        .then(() => {
          this.$toast({
            message: '绑定成功，现在可以用手机登录！',
            onClose: () => {
              this.$router.back();
            },
          });
        })
        .catch(err => {
          console.log(err);
          this.$toast.clear();
          this.$toast(err || '登录失败', 2000);
        });
    },

    clear(data, key) {
      data[key] = '';
    },
  },
};
</script>
